.search-panel {
  margin-top: 30px;
  padding: 25px;
  background-color: #3d5a80;
  border-radius: 4px;
  box-shadow: 15px 15px 30px rgb(0,0,0, .15);
  color: #fff;
}

@media (max-width: 500px) {
  .search-panel {
    padding: 10px;
  }

  .search-panel input::placeholder {
    font-size: 14px;
  }
}