.app-add-form {
  margin-top: 30px;
  padding: 25px;
  background-color: #3d5a80;
  border-radius: 4px;
  box-shadow: 15px 15px 30px rgba(0,0,0, .15);
  color: #fff;
}

.add-form {
  margin-top: 20px;
}

.add-form input {
  margin-right: 20px;
}

@media (max-width: 500px) {
  .app-add-form {
    padding: 2px;
  }

  .app-add-form h3 {
  font-size: 16px;
  text-align: center;
  }

  .app-add-form .add-form.d-flex input {
    margin-right: 5px;
    padding: 2px;
    font-size: 12px;
  }

  .app-add-form .add-form.d-flex button {
    margin-right: 5px;
    padding: 5px;
    font-size: 14px;
  }
}